  async takePicture(op) {
  const image = await Plugins.Camera.getPhoto({
  correctOrientation: true,
            height: 200,
            width: 200,
                quality: 60,
  allowEditing: true,
            resultType: CameraResultType.DataUrl,
            source: CameraSource.Camera
          });
          this.authenticationService.generateFromImage(image.dataUrl, 700, 700, 1, data => {
            switch (op) {
              case 'foto0a': this.foto0img = data;   this.Form.controls['foto0'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto0'].value)); break;
              case 'foto1a': this.foto1img = data;   this.Form.controls['foto1'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto1'].value)); break;
              case 'foto2a': this.foto2img = data;   this.Form.controls['foto2'].setValue( this.authenticationService.procesafoto(this.Form.controls['foto2'].value)); break;
              case 'logo1a': this.logo1img = data;   this.Form.controls['logo1'].setValue( this.authenticationService.procesafoto(this.Form.controls['logo1'].value)); break;
              case 'logo2a': this.logo2img = data;   this.Form.controls['logo2'].setValue( this.authenticationService.procesafoto(this.Form.controls['logo2'].value)); break;

                default:
            }
        });
        }
